import Header from "../../common/Header";
import AppSection from "../../common/Section";
import { MainContainer } from "../../GlobalStyle";



const Author = () => (
    <MainContainer>
        <Header title="O autorze" />
        <AppSection
            title="Rafa Olewicz"

            body={
                <>                   
                    <p>
                        <b>Od wielu lat pasjonuj si elektronik</b>, ktra staa si moim hobby. <br/>
                        Interesuj si take technologi, fotografi oraz muzyk; jestem samoukiem w grze na perkusji,
                        ktra dodaje mi energii i pozytywnych wrae, obecnie jest to moje ulubione hobby. <br/>
                        Cho obecnie nie mam okazji kontynuowa swoich dziaa w dziedzinie elektroniki,
                        programowanie wci fascynuje mnie od momentu, gdy po raz pierwszy signem po <i>Arduino</i>.

                        <h3>Dlaczego programowanie?</h3>

                        Programowanie stao si moim sposobem na spdzanie czasu. Kiedy siadam do komputera, <br/>
                        czas mija niezauwaalnie, co potwierdza moj pasj do tej dziedziny.<br/>
                        Obecnie korzystam z kursu w szkole <b>YouCode</b>, co daje mi <em>moliwo rozwijania umiejtnoci</em><br/> 
                        i mam nadziej na <em>zmian swojego stylu ycia oraz dalszy rozwj</em> w obszarze technologii.
                    </p>
                </>
            }
        />
    </MainContainer>
);

export default Author